<template>
    <div class="article-box w960">
            <el-form ref="form" :model="this.formData" label-width="80px">
            <el-form-item label="头像">
                <img :src="formData.author.avatar">
            </el-form-item>
            <el-form-item label="作者">
                <el-input v-model="formData.author.username"></el-input>
            </el-form-item>
            <el-form-item label="作者">
                <el-input v-model="formData.title"></el-input>
            </el-form-item>
            <el-form-item label="分类">
                <el-input v-model="formData.category.name"></el-input>
            </el-form-item>
            <el-form-item label="上传时间">
                <el-input v-model="formData.createdTime"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="formData.category.name"></el-input>
            </el-form-item>
            <el-form-item label="内容">
                <br>
                <el-input type="textarea" v-html="formData.content"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" >更改</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                formData:{
                    author:{},
                    category:{},
                    content:'',
                    title:'',
                    createdTime:'',
                    updateTime:''

                },
                id:''
            }
        },
        methods:{
            getData(){
                this.$axios.get(`/article/${this.$route.query.id}`).then(res =>{
                    if(res.code == 200){
                        this.formData = res.data
                        console.log(res)
                        let time = new Date(res.data.createdTime)
                        this.formData.createdTime = (time.toLocaleDateString()+ "" + time.toTimeString().substr(0, 8))
                        console.log(this.formData.createdTime)
                    }
                })
            }
        },
        created(){
            this.getData()
        }
    }
</script>

<style scoped>
.article-box{
    margin: 25px auto;
}
  img{
    width: 100px;
    height: 100px;
  }
</style>


